import React from 'react';
import PropTypes from 'prop-types';
import './style.css';

const SourceInput = (props) => {
  return(
    <div className="SourceInput">
      <input
        className="SourceInputField"
        value={props.lookupValue}
        placeholder={props.sourceLanguage.name}
        onChange={props.handleLookupChange}
        onKeyPress={props.handleLookupKeyPress}
      />
    </div>
  )
};

SourceInput.propTypes = {
  lookupValue: PropTypes.string,
  sourceLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  }),
  handleLookupChange: PropTypes.func.isRequired,
  handleLookupKeyPress: PropTypes.func.isRequired
};

SourceInput.defaultProps = {
  lookupValue: "",
  sourceLanguage: {
    name: "",
    code: ""
  }
};

export default SourceInput;
