import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './style.css';

const panlex = require('panlex');
// const posTagger = require( 'wink-pos-tagger' );
const natural = require('natural');
var tagger

export default class Prompt extends Component {
  constructor(props) {
    super(props);
    this.state = {
      translationReady: false,
      translationDict: {},
      voices: []
    }
  }

  componentDidMount() {
    const getVoices = () => {
      return new Promise(resolve => {
        let voices = speechSynthesis.getVoices()
        if (voices.length) {
          resolve(voices)
          return
        }
        speechSynthesis.onvoiceschanged = () => {
          voices = speechSynthesis.getVoices()
          resolve(voices)
        }
      })
    }
    const saveVoices = async () => {
      let voices = await getVoices()
      this.setState({voices: voices})
    }

    saveVoices()
    // tagger = posTagger();
    tagger = new natural.WordTokenizer();
    this.preparePrompt()
  }

  componentDidUpdate(prevProps) {
    if (
      (prevProps.sourceLanguage!==this.props.sourceLanguage) ||
      (prevProps.currentPrompt!==this.props.currentPrompt)
    ){
      this.preparePrompt()
      if (this.props.focus) {
        setTimeout(this.playAudio(this.props.currentPrompt.text), 200);
        return
      }
    }
    if (this.props.focus && !prevProps.focus) {
      setTimeout(this.playAudio(this.props.currentPrompt.text), 500)
    }
  }

  _saveInState = (result, word, lemma) => {
    result.sort(function(a, b) {
      return a.trans_quality<b.trans_quality ? 1 : -1
    });
    this.setState(prevState => ({
      ...prevState,
      translationDict: {
        ...prevState.translationDict,
        [word]: {
          [lemma]: result.slice(0, 3)
        }
      }
    }));
  }

  _transQuery = (sourceCode, word_id, word, lemma) => {
    panlex.query('/expr', {
      "uid": sourceCode,
      "trans_expr": word_id,
      "include": "trans_quality"
    }, (err, data) => {this._saveInState(data.result, word, lemma)});
  }

  _getTransExp = (sourceCode, targetCode, word, lemma) => {
    panlex.query('/expr', {
      "uid": targetCode,
      "txt": lemma
    }, (err, data) => {
      try{this._transQuery(
        sourceCode, data.result[0].id, word, lemma
      )}
      catch(err){
        this.setState(prevState => ({
          ...prevState,
          translationDict: {
            ...prevState.translationDict,
            [word]: {
              [lemma]: []
            }
          }
        }))
      };
    });
  }

  preparePrompt = () => {
    this.tags = tagger.tokenize(this.props.currentPrompt.text)
    console.log(tagger.tokenize(this.props.currentPrompt.text))
    for (let i=0; i<this.tags.length; i++) {
      if (true) {
        console.log(this.tags[i])
        let word = this.tags[i]
        let lemma = this.tags[i]
        // if (this.props.targetLanguage.code==="eng-000") {
        //   lemma = (this.tags[i].hasOwnProperty("lemma"))
        //     ? this.tags[i].lemma
        //     : this.tags[i].normal
        // } else {lemma = word}
        this._getTransExp(
          this.props.sourceLanguage.code,
          this.props.targetLanguage.code,
          word,
          lemma
        )
      }
    }
    this.setState(prevState => ({
      translationReady: true,
    }))
  }

  playAudio = (word) => {
    let msg = new SpeechSynthesisUtterance(word.toLowerCase());
    let german_voice = this.state.voices[4]
    let english_voice = this.state.voices[0]
    msg.voice = (
      this.props.targetLanguage.code==="deu-000"
    ) ? german_voice : english_voice
    msg.lang = (
      this.props.targetLanguage.code==="deu-000"
    ) ? "de" : "en"
    window.speechSynthesis.speak(msg);
  }

  render() {
    let promptDisplay
    if (!this.state.translationReady) {
      promptDisplay = ""
    } else {
      promptDisplay = this.tags.map((object, i) => (
          <span key={i} className="WordBox"
                onClick={() => this.playAudio(object)}>
            {" "}{object}
            <span className="TransBox">{
              (() => {
                if (this.state.translationDict.hasOwnProperty(object)
                    &&true) {
                  let lemma
                  if (this.props.targetLanguage.code==="eng-000") {
                    lemma = (this.tags[i].hasOwnProperty("lemma"))
                      ? this.tags[i]
                      : this.tags[i]
                  } else {lemma = object}
                  let maxScore = (
                    this.state.translationDict[object][lemma].length
                  ) ?
                    this.state.translationDict[object][lemma][0].trans_quality : 0
                  return this.state.translationDict[object][lemma].map(
                    (trans, j) => (
                      <span key={j}>
                        <div className="TranslationWord">
                          {j+1}. {trans.txt}<br/>
                        </div>
                        <div className="QualityOuter">
                          <div className="QualityScore"
                          style={{width: 100*trans.trans_quality/maxScore.toString()+"%"}}>
                          </div>
                        </div>
                      </span>
                    )
                  )
                }
              })()}
            </span>
          </span>
        )
      )
    }
    return(
      <div className="Prompt">
        {promptDisplay}
      </div>
    )
  }
};

Prompt.propTypes = {
  currentPrompt: PropTypes.object,
  sourceLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  }),
  targetLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  }),
};

Prompt.defaultProps = {
  currentPrompt: {},
  sourceLanguage: {
    name: "Deutsch",
    code: "deu-000"
  },
  targetLanguage: PropTypes.shape({
    name: "English",
    code: "eng-000"
  }),
};
