import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './style.css';

export default class SummaryPopup extends Component {
  constructor(props) {
    super(props)
    this.state = {
      indecesDisplayed: Array.from(
        {length:this.props.promptsInSession.length},
        (v,k)=>k
      )
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.promptsInSession!==this.props.promptsInSession) {
      this.setState(() => ({
        indecesDisplayed: Array.from(
          {length:this.props.promptsInSession.length},
          (v,k)=>k
        )
      }))
    }
  }

  onButtonClick = (feedback, promptID, index) => {
    this.props.handleDifficultyFeedback(feedback, promptID)

    this.setState((prevState) => ({
      indecesDisplayed: prevState.indecesDisplayed.filter(
        (e, i) => i!==index
      )
    }), () => {
      if (this.state.indecesDisplayed.length===0) {
        this.props.closeSummaryPopup()
      }
    })
  }

  render() {
    if (!this.props.show) {
      return null
    }
    let promptsDisplayed = this.state.indecesDisplayed.map(
      i => this.props.promptsInSession[i]
    )
    let responsesDisplayed = this.state.indecesDisplayed.map(
      i => this.props.responsesInSession[i]
    )
    return (
      <div className="Background">
        <div className="SummaryPopup">
          <div className="SummaryPopupHeader">
            {
              (this.props.targetLanguage.code==="eng-000")
              ? "Review"
              : "Rückblick"
            }
          </div>
          {promptsDisplayed.map((prompt, index) => {
            return (
              <div key={index} className="PromptBox">
                <div className="PromptColumn">
                  <h1>
                    {
                      (this.props.targetLanguage.code==="eng-000")
                      ? "Message"
                      : "Nachricht"
                    }
                  </h1>
                  {prompt.text}
                </div>
                <div className="ResponseColumn">
                  <h1>
                    {
                      (this.props.targetLanguage.code==="eng-000")
                      ? "Your response"
                      : "Deine Antwort"
                    }
                  </h1>
                  {responsesDisplayed[index]}
                </div>
                <div className="ExamplesColumn">
                  <h1>
                  {
                    (this.props.targetLanguage.code==="eng-000")
                    ? "Sample responses"
                    : "Beispielantworten"
                  }
                  </h1>
                  {prompt.responses.slice(0,3).map(
                    (item, index) => (<ul key={index}>{item}</ul>)
                  )}
                </div>
                <div className="ButtonsColumn">
                  <button
                    className="Button Green"
                    onClick={() => this.onButtonClick(0, prompt.id, index)}>
                    {
                      (this.props.targetLanguage.code==="eng-000")
                      ? "Easy"
                      : "Leicht"
                    }
                  </button>
                  <button
                    className="Button Yellow"
                    onClick={() => this.onButtonClick(1, prompt.id, index)}>
                    {
                      (this.props.targetLanguage.code==="eng-000")
                      ? "Ok"
                      : "Ok"
                    }
                  </button>
                  <button
                    className="Button Red"
                    onClick={() => this.onButtonClick(2, prompt.id, index)}>
                    {
                      (this.props.targetLanguage.code==="eng-000")
                      ? "Hard"
                      : "Schwer"
                    }
                  </button>
                </div>
              </div>
            )
          })}
        </div>
      </div>
    )
  }
};

SummaryPopup.propTypes = {
  show: PropTypes.bool,
  targetLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  }),
  promptsInSession: PropTypes.arrayOf(PropTypes.object),
  responsesInSession: PropTypes.arrayOf(PropTypes.string),
  closeSummaryPopup:PropTypes.func.isRequired,
  handleDifficultyFeedback: PropTypes.func.isRequired
};

SummaryPopup.defaultProps = {
  show: false,
  targetLanguage: {name: "English", code: "eng-000"},
  promptsInSession: {},
  responsesInSession: {}
};
