import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import './style.css';
import ExitButton from './ExitButton';
import Languages from '../../content/Languages.js'

const LanguagePopup = (props) => {
  return props.show && (
      <div className="Background">
        <div className="LanguagePopup">
          <div className="LanguagePopupHeader">
            {
              (props.targetLanguage.code==="eng-000")
              ? "Your Language"
              : "Deine Sprache"
            }
            <ExitButton closeLanguagePopup={props.closeLanguagePopup}/>
          </div>
          <div>
            <datalist id="languages">
              {Languages.result.map((item) =>
                  <option value={item.uid} key={item.uid}>
                    {item.name_expr_txt}
                  </option>
              )}
            </datalist>
            <input
              autoFocus
              placeholder={
                (props.targetLanguage.code==="eng-000")
                ? "Search.."
                : "Suche.."
              }
              className="LanguageInput"
              autoComplete="on"
              list="languages"
              onChange={props.handleLanguageChange}
              onKeyPress={props.handleLanguageKeyPress}
            />
            <FontAwesomeIcon
              icon="check-square"
              className="SubmitLanguage"
              onClick={props.handleLanguageSubmit}
            />
          </div>
        </div>
      </div>
  )
};

LanguagePopup.propTypes = {
  show: PropTypes.bool,
  targetLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  }),
  closeLanguagePopup: PropTypes.func.isRequired,
  handleLanguageChange: PropTypes.func.isRequired,
  handleLanguageSubmit: PropTypes.func.isRequired,
  handleLanguageKeyPress: PropTypes.func.isRequired
};

LanguagePopup.defaultProps = {
  show: false,
  targetLanguage: {name: "English", code: "eng-000"}
};

export default LanguagePopup;
