import React from 'react';
import PropTypes from 'prop-types';
import ReactPlayer from 'react-player'
import './style.css';
import ExitButton from './ExitButton';

const HelpPopup = (props) => {
  return props.show && (
      <div className="Background">
        <div className="HelpPopup">
          <div className="HelpPopupHeader">
            {
              (props.targetLanguage.code==="eng-000")
              ? "Help"
              : "Hilfe"
            }
            <ExitButton closeHelpPopup={props.closeHelpPopup}/>
          </div>
          <div className="HelpPopupVideo">
            <ReactPlayer
              className="ReactPlayer"
              url="https://youtu.be/JTCrT8fvhF0"
              playing
            />
          </div>
        </div>
      </div>
  )
};

HelpPopup.propTypes = {
  show: PropTypes.bool,
  targetLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  }),
  closeHelpPopup:PropTypes.func.isRequired
};

HelpPopup.defaultProps = {
  show: false,
  targetLanguage: {name: "English", code: "eng-000"}
};

export default HelpPopup
