import React from 'react';
import PropTypes from 'prop-types';
import './style.css';

const TargetOutput = (props) => {
  let transFound,
      transOut,
      nTrans,
      maxScore
  if (props.translationArray.length>0) {
    transFound = true
    nTrans = Math.min(Math.max(3, props.translationArray.length), 3)
    maxScore = props.translationArray[0].trans_quality
    transOut = props.translationArray.slice(0,nTrans).map(
      (value, index, array) => (
      <div key={index} className="TranslationLine">
        <div className="TranslationWord">
          {index+1}. {value.txt}</div><div className="QualityOuter">
          <div
            className="QualityScore"
            style={{width: 100*value.trans_quality/maxScore.toString()+"%"}}>
          </div>
        </div>
      </div>)
    )
  } else {
    transFound = false
    transOut = ""
  }
  return(
    <div className="TargetOutput">
      <div className={
        transFound? "TargetOutputField" : "TargetOutputField HiddenField"
      }>
        <div>{transOut}</div>
      </div>
    </div>
  )
};

TargetOutput.propTypes = {
  translationArray: PropTypes.array
};

TargetOutput.defaultProps = {
  translationArray: []
};

export default TargetOutput;
