import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './style.css';
import SourceInput from './SourceInput'
import TranslateButton from './TranslateButton'
import TargetOutput from './TargetOutput'

const panlex = require('panlex')

export default class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lookupValue: "",
      translationArray: []
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.sourceLanguage!==this.props.sourceLanguage) {
      this.setState(prevState => ({
        lookupValue: "",
        translationArray: []
      }))
    }
  }

  _saveInState = (r) => {
    r.sort(function(a, b) {return a.trans_quality<b.trans_quality ? 1 : -1});
    this.setState(prevState => ({translationArray: r}));
  }

  _transQuery = (targetCode, word_id) => {
    panlex.query('/expr', {
      "uid": targetCode,
      "trans_expr": word_id,
      "include": "trans_quality"
    }, (err, data) => {this._saveInState(data.result)});
  }

  _getTransExp = (sourceCode, targetCode, word) => {
    panlex.query('/expr', {
      "uid": sourceCode,
      "txt": word
    }, (err, data) => {
      try{this._transQuery(targetCode, data.result[0].id)}
      catch(err){
        this.setState(prevState => ({translationArray: []}))
      };
    });
  }

  handleLookupChange = (e) => {
    let input = e.target.value
    this.setState(prevState => ({lookupValue: input}))
  }

  handleLookupSubmit = () => {
    this._getTransExp(
      this.props.sourceLanguage.code,
      this.props.targetLanguage.code,
      this.state.lookupValue
    )
  }

  handleLookupKeyPress = (key) => {
    if (key.charCode===13) {
      this._getTransExp(
        this.props.sourceLanguage.code,
        this.props.targetLanguage.code,
        this.state.lookupValue
      )
    }
  }

  render() {
    return(
      <div className="Footer">
        <SourceInput
          sourceLanguage={this.props.sourceLanguage}
          lookupValue={this.state.lookupValue}
          handleLookupChange={this.handleLookupChange}
          handleLookupKeyPress={this.handleLookupKeyPress}
        />
        <TranslateButton
          handleLookupSubmit={this.handleLookupSubmit}
        />
        <TargetOutput
          translationArray={this.state.translationArray}
        />
        <div className="Copyright">© 2019 Julian Grosse</div>
      </div>
    )
  }
};

Footer.propTypes = {
  sourceLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  }),
  targetLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  })
};

Footer.defaultProps = {
  sourceLanguage: {
    name: "",
    code: ""
  },
  targetLanguage: {
    name: "",
    code: ""
  }
};
