import React from 'react';
import PropTypes from 'prop-types';
import './style.css';

const StartingPopup = (props) => {
  return props.show && (
    <div className="StartingPopupBackground">
      <div className="StartingPopup">
        <div
          id="deu-000"
          className="TargetLanguageButton"
          onClick={props.setTargetLanguage}
        >
          <p id="deu-000">Ich lerne</p><h1 id="deu-000">Deutsch</h1>
        </div>
        <div
          id="eng-000"
          className="TargetLanguageButton"
          onClick={props.setTargetLanguage}
        >
          <p id="eng-000">I'm learning</p><h1 id="eng-000">English</h1>
        </div>
      </div>
    </div>
  )
}

StartingPopup.propTypes = {
  show: PropTypes.bool,
  setTargetLanguage: PropTypes.func.isRequired
}

StartingPopup.defaultProps = {
  show: false
}

export default StartingPopup
