import React from 'react';
import PropTypes from 'prop-types';
import './style.css';
import Prompt from './Prompt'
import Response from './Response'

const Body = (props) => {
  return(
    <div className="Body">
      <Prompt
        focus={props.focus}
        currentPrompt={props.currentPrompt}
        sourceLanguage={props.sourceLanguage}
        targetLanguage={props.targetLanguage}
      />
      <Response
        focus={props.focus}
        currentResponse={props.currentResponse}
        handleResponseChange={props.handleResponseChange}
        handleResponseSubmit={props.handleResponseSubmit}
        handleResponseKeyPress={props.handleResponseKeyPress}
      />
    </div>
  )
};

Body.propTypes = {
  focus: PropTypes.bool,
  currentPrompt: PropTypes.object,
  currentResponse: PropTypes.string,
  sourceLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  }),
  targetLanguage: PropTypes.shape({
    name: PropTypes.string,
    code: PropTypes.string
  }),
  handleResponseChange: PropTypes.func.isRequired,
  handleResponseSubmit: PropTypes.func.isRequired,
  handleResponseKeyPress: PropTypes.func.isRequired,
};

Body.defaultProps = {
  focus: false,
  currentPrompt: {},
  currentResponse: "",
  sourceLanguage: {},
  targetLanguage: {},
};

export default Body;
