import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import './style.css';

export default class Response extends Component {
  componentDidUpdate() {
    if (this.props.focus) {
      document.getElementById("InputField").focus();
    }
  }

  render() {
    return(
      <div className="Response">
        <input
          id="InputField"
          type="text"
          className="InputField"
          autoComplete="off"
          autoFocus={this.props.focus}
          value={this.props.currentResponse}
          onChange={this.props.handleResponseChange}
          onKeyPress={this.props.handleResponseKeyPress}
        />
        <FontAwesomeIcon
          icon="chevron-right"
          className="SubmitResponse"
          onClick={this.props.handleResponseSubmit}
        />
      </div>
    )
  }
};

Response.propTypes = {
  focus: PropTypes.bool,
  currentResponse: PropTypes.string,
  handleResponseChange: PropTypes.func.isRequired,
  handleResponseSubmit: PropTypes.func.isRequired,
  handleResponseKeyPress: PropTypes.func.isRequired
};

Response.defaultProps = {
  focus: false,
  currentResponse: ""
};
