import React, { Component } from 'react';
import { library } from '@fortawesome/fontawesome-svg-core'
import { fas } from '@fortawesome/free-solid-svg-icons'
import './App.css';

import Header from './Header'
import Body from './Body'
import Footer from './Footer'
import StartingPopup from './StartingPopup'
import LanguagePopup from './LanguagePopup'
import SummaryPopup from './SummaryPopup'
import HelpPopup from './HelpPopup'
import Languages from '../content/Languages.js'
import EnglishPrompts from '../content/EnglishPrompts.js'
import GermanPrompts from '../content/GermanPrompts.js'

library.add(fas)
const uids = Languages.result.map(x => x.uid);

export default class App extends Component {
  state = {
    sourceLanguage: {name: "Deutsch", code: "deu-000"},
    targetLanguage: {name: "English", code: "eng-000"},
    sourceLanguageInput: "",
    promptsPerSession: 3,
    promptNumber: 1,
    allPrompts: [],
    promptsIDs: [],
    currentPrompt: {"id": -1, "text": "", "responses": []},
    promptsInSession: [],
    currentResponse: "",
    responsesInSession: [],
    showStartingPopup: true,
    showLanguagePopup: false,
    showSummaryPopup: false,
    showHelpPopup: false
  }

  getFirstPrompt = () => {
    let selectedPrompt =  this.state.allPrompts.filter(
      e => (e.id===0)
    )[0]
    let n = this.state.allPrompts.length-1
    this.setState(prevState => ({
      currentPrompt: selectedPrompt,
      promptsIDs: Array.from(new Array(n),(val,index)=>index+1)
    }))
  }

  toggleStartingPopup = () => {
    this.setState(prevState => ({
      showStartingPopup: !prevState.showStartingPopup
    }))
  }

  setTargetLanguage = (e) => {
    switch (e.target.id) {
      case "deu-000":
        this.setState({
          targetLanguage: {name: "Deutsch", code: "deu-000"},
          allPrompts: GermanPrompts.firststage,
          showStartingPopup: false,
          showLanguagePopup: true
        }, this.getFirstPrompt);
        break;
      case "eng-000":
        this.setState({
          targetLanguage: {name: "English", code: "eng-000"},
          allPrompts: EnglishPrompts.firststage,
          showStartingPopup: false,
          showLanguagePopup: true
        }, this.getFirstPrompt);
        break;
      default:
        break
    }
  }

  toggleHelpPopup = () => {
    this.setState(prevState => ({
      showHelpPopup: !prevState.showHelpPopup
    }))
  }

  toggleSummaryPopup = () => {
    this.setState(prevState => ({
      showSummaryPopup: !prevState.showSummaryPopup,
      promptsInSession: [],
      responsesInSession: []
    }))
  }

  toggleLanguagePopup = () => {
    this.setState(prevState => ({
      showLanguagePopup: !prevState.showLanguagePopup
    }))
  }

  handleLanguageChange = (e) => {
    let input = e.target.value
    this.setState(prevState => ({sourceLanguageInput: input}))
  }

  handleLanguageSubmit = () => {
    if (uids.includes(this.state.sourceLanguageInput)){
      let languageName = Languages.result.filter(obj => {
        return obj.uid === this.state.sourceLanguageInput
      })[0].name_expr_txt
      this.setState(prevState => ({
        sourceLanguage: {
          name: languageName,
          code: this.state.sourceLanguageInput
        },
        showLanguagePopup: false,
      }))
    }
  }

  handleLanguageKeyPress = (key) => {
    if ((key.charCode===13) && this.state.showLanguagePopup) {
      this.handleLanguageSubmit()
    }
  }

  handleResponseChange = (e) => {
    let input = e.target.value
    this.setState(prevState => ({currentResponse: input}))
  }

  handleResponseSubmit = () => {
    let selectedPrompt =  this.state.allPrompts.filter(
      e => (e.id === this.state.promptsIDs[0])
    )[0]

    this.setState(prevState => ({
      promptNumber: prevState.promptNumber+1,
      currentResponse: "",
      promptsIDs: prevState.promptsIDs.slice(1, prevState.promptsIDs.length),
      responsesInSession: [
        ...prevState.responsesInSession,
        this.state.currentResponse
      ],
      currentPrompt: selectedPrompt,
      promptsInSession: [
        ...prevState.promptsInSession,
        this.state.currentPrompt
      ],
      showSummaryPopup:
        (this.state.promptNumber % this.state.promptsPerSession===0)
        ? true
        : false
    }))
  }

  handleResponseKeyPress = (key) => {
    if ((key.charCode===13) && !this.state.showSummaryPopup) {
      this.handleResponseSubmit()
    }
  }

  handleDifficultyFeedback = (feedback, promptID) => {
    switch (feedback) {
      case 0:
        this.setState(prevState => ({
          promptsIDs: prevState.promptsIDs.concat([promptID])
        }))
        break;
      case 1:
        this.setState(prevState => ({
          promptsIDs: prevState.promptsIDs.slice(0, 3).concat(
            [promptID]).concat(
              prevState.promptsIDs.slice(3, prevState.promptsIDs.length))
        }))
        break;
      case 2:
        this.setState(prevState => ({
          promptsIDs: [promptID].concat(prevState.promptsIDs)
        }))
        break;
      default:
        this.setState(prevState => ({
          promptsIDs: prevState.promptsIDs.concat([promptID])
        }))
    }
  }

  render() {
    let progressValue = (
      (this.state.promptNumber-1)%this.state.promptsPerSession)
      /this.state.promptsPerSession
    return (
      <div className="App">
        <Header
          openLanguagePopup={this.toggleLanguagePopup}
          openHelpPopup={this.toggleHelpPopup}
          progressValue={progressValue}
        />
        <Body
          focus={
            !this.state.showLanguagePopup
            &&!this.state.showSummaryPopup
            &&!this.state.showHelpPopup
            &&!this.state.showStartingPopup
          }
          currentPrompt={this.state.currentPrompt}
          currentResponse={this.state.currentResponse}
          sourceLanguage={this.state.sourceLanguage}
          targetLanguage={this.state.targetLanguage}
          handleResponseChange={this.handleResponseChange}
          handleResponseSubmit={this.handleResponseSubmit}
          handleResponseKeyPress={this.handleResponseKeyPress}
        />
        <Footer
          sourceLanguage={this.state.sourceLanguage}
          targetLanguage={this.state.targetLanguage}
        />
        <StartingPopup
          show={this.state.showStartingPopup}
          setTargetLanguage={this.setTargetLanguage}
        />
        <LanguagePopup
          show={this.state.showLanguagePopup}
          targetLanguage={this.state.targetLanguage}
          closeLanguagePopup={this.toggleLanguagePopup}
          handleLanguageChange={this.handleLanguageChange}
          handleLanguageSubmit={this.handleLanguageSubmit}
          handleLanguageKeyPress={this.handleLanguageKeyPress}
        />
        <HelpPopup
          show={this.state.showHelpPopup}
          targetLanguage={this.state.targetLanguage}
          closeHelpPopup={this.toggleHelpPopup}
        />
        <SummaryPopup
          show={this.state.showSummaryPopup}
          targetLanguage={this.state.targetLanguage}
          promptsInSession={this.state.promptsInSession}
          responsesInSession={this.state.responsesInSession}
          handleDifficultyFeedback={this.handleDifficultyFeedback}
          closeSummaryPopup={this.toggleSummaryPopup}
        />
      </div>
    );
  }
}
