import React from 'react';
import PropTypes from 'prop-types';
import './style.css';
import LanguageMenu from './LanguageMenu'
import ProgressBar from './ProgressBar'
import HelpButton from './HelpButton'

const Header = (props) => {
  return(
    <div className="Header">
      <LanguageMenu
        openLanguagePopup={props.openLanguagePopup}
      />
      <ProgressBar progressValue={props.progressValue}/>
      <HelpButton
        openHelpPopup={props.openHelpPopup}
      />
    </div>
  )
};

Header.propTypes = {
  openLanguagePopup: PropTypes.func.isRequired,
  openHelpPopup: PropTypes.func.isRequired,
  progressValue: PropTypes.number
};

Header.defaultProps = {
  progressValue: 0
};

export default Header;
